      description:
        'Разработка ПТ-САУ WZ-111-1G FT велась с учётом опыта создания тяжёлого танка WZ-111. Прототип последнего был создан в начале 1960-х годов. Он прошёл испытания пробегом, в ходе которых было выявлено множество недостатков в конструкции машины, и дальнейшие работы над WZ-111 были свёрнуты в 1964 году. В конструкции WZ-111-1G FT угадывается влияние советской школы танкостроения. Данных о производстве ПТ-САУ в металле нет.',
