        'В 1956–1957 годах слушатели Академии БТВ разработали дипломный проект установки механизма заряжания для тяжёлых танков ИС-3 и Т-10. В варианте для ИС-3 была предусмотрена двухрядная боеукладка магазинного типа. Экипаж был сокращён до трёх человек: командир, наводчик, механик-водитель. По официальным данным этот танк так и остался проектным образцом, но британская разведка докладывала, что было произведено несколько десятков тысяч танков, законсервированных на секретном складе.',
